<template>
  <div style="padding:20px">

    <el-form :inline="true" @submit.native.prevent>
      <el-form-item label="">
        <el-select size="medium" v-model="queryObj.dealSysCode" placeholder="" clearable filterable>
          <el-option v-for="item in allSysCodes" :key="item" :label="item" :value="item">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input size="medium" v-model.trim="queryObj.taskName" placeholder="" clearable style="width:200px" @keyup.enter.native="onQuery"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select size="medium" v-model="queryObj.state" placeholder="" clearable filterable>
          <el-option label="" value="START"></el-option>
          <el-option label="" value="STOP"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button size="medium" type="primary" @click="onQuery"></el-button>
        <el-button size="medium" type="success" @click="newObject"></el-button>
      </el-form-item>
    </el-form>

    <el-table ref="singleTable" :data="dataList" highlight-current-row border style="width: 100%" v-loading="tableLoading" size="mini" @row-dblclick="onShow" :header-cell-style="{background:'#F3F4F7',color:'#555'}" row-key='id'>
      <el-table-column property="dealSysCode" label="" align="center" width="100"></el-table-column>
      <el-table-column property="tag" label="tag" align="center" width="100"></el-table-column>
      <el-table-column property="taskName" label="" min-width="140" align='center'> </el-table-column>
      <el-table-column property="remark" label="" align="center" min-width="140"> </el-table-column>
      <el-table-column property="cluster" label="" align="center" width="80"> </el-table-column>
      <!-- <el-table-column property="assignNum" label="" align="center" width="90"> </el-table-column> -->
      <!-- <el-table-column property="threadNumber" label="" align="center" width="60"> </el-table-column> -->
      <el-table-column property="permitRunStartTime" label="" align="center" min-width="150"> </el-table-column>
      <el-table-column property="state" label="" align="center" width="90">
