          <i v-if="taskChangeStateMap[scope.row.id]" class="el-icon-loading"></i>
          <el-tag size="mini" v-if="taskChangeResultMap[scope.row.id+'succCount'] > 0" type="success">{{taskChangeResultMap[scope.row.id+'succCount']}}</el-tag>
          <el-tag size="mini" v-if="taskChangeResultMap[scope.row.id+'failCount'] > 0" type="danger">{{taskChangeResultMap[scope.row.id+'failCount']}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column property="dateUpdated" label="" align="center" width="150" :formatter="dateFormat"> </el-table-column>
      <el-table-column property="updatedBy" label="" align="center" min-width="80"> </el-table-column>

      <el-table-column property="operation" fixed="right" label="" align="center" width="300">
        <template slot-scope="scope">
          <el-button v-if="scope.row.state==='STOP'" plain size="mini" type="info" @click.stop="startTask(scope.row)"></el-button>
          <el-button v-if="scope.row.state==='START'" plain size="mini" type="warning" @click.stop="stopTask(scope.row)"></el-button>
          <el-button size="mini" type="primary" @click.stop="editObject(scope.row)"></el-button>
          <el-button size="mini" type="warning" @click.stop="copyObject(scope.row)"></el-button>
          <el-button size="mini" type="danger" @click.stop="deleteObject(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-row type="flex" justify="center" style="margin-top:15px;">
      <el-pagination background layout="total, prev, pager, next, jumper" :current-page.sync="queryObj.pageNum" @current-change="onQuery" :total="total" :page-size="queryObj.pageSize">
      </el-pagination>
    </el-row>

    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="1280px">
      <el-form label-position='right' label-width="170px" size="medium" :model="detailObject" ref='objForm'>
        <el-row>
          <el-col :span="8">
            <el-form-item label="tag" prop="tag" :rules="rule">
              <el-select v-model="detailObject.tag" placeholder="" :disabled="this.EditType=='edit'" filterable>
                <el-option v-for="item in allTagCodes" :key="item" :label="item" :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="dealSysCode" :rules="rule">
              <el-select v-model="detailObject.dealSysCode" placeholder="" :disabled="this.EditType=='edit'" filterable>
                <el-option v-for="item in allSysCodes" :key="item" :label="item" :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="cluster" :rules="rule">
              <el-select v-model="detailObject.cluster" placeholder="" filterable>
                <el-option v-for="item in clusterList" :key="item.cluster" :label="item.cluster" :value="item.cluster">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="8">
            <el-form-item label="" prop="processorType" :rules="rule">
                <el-select v-model="detailObject.processorType" placeholder="" clearable filterable>
                  <el-option label="Execute()" value="Execute"></el-option>
                  <el-option label="SelectExecuteFlow()" value="SelectExecuteFlow"></el-option>
                  <el-option label="SelectExecuteSimple()" value="SelectExecuteSimple"></el-option>
                </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="16">
            <el-form-item label="Bean" prop="dealBizBeanName" :rules="rule">
              <el-input v-model.trim="detailObject.dealBizBeanName" maxlength="64" show-word-limit></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="taskName" :rules="rule">
              <el-input v-model.trim="detailObject.taskName" maxlength="64" show-word-limit></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="permitRunStartTime" :rules="rule">
              <cron-input v-model="detailObject.permitRunStartTime" @change="changeCron" />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model.number="detailObject.sleepTimeNoData" :disabled="detailObject.processorType!='SelectExecuteFlow'"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model.number="detailObject.sleepTimeInterval" :disabled="detailObject.processorType=='Execute'"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model.number="detailObject.fetchDataNumber" :disabled="detailObject.processorType=='Execute'"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model.number="detailObject.executeNumber" :disabled="detailObject.processorType =='Execute'"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model.number="detailObject.assignNum"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model.number="detailObject.threadNumber" :disabled="detailObject.processorType=='Execute'"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
         <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <el-input v-model.trim="detailObject.taskItems"></el-input>
            </el-form-item>
          </el-col>
           <el-col :span="12">
             <el-form-item label="">
              <el-input v-model.trim="detailObject.remark" maxlength="64" show-word-limit></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="">
          <el-input type="textarea" :rows="3" v-model.trim="detailObject.taskParameter"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="validateObj"> </el-button>
      </span>

    </el-dialog>

    <el-dialog :title="runtimeDialogTitle" :visible.sync="runtimeDialogVisible" width="90%" @close="runtimeDialogClose">
      <el-table ref="singleTable" :data="runtimeList" highlight-current-row border style="width: 100%" v-loading="runtimeTableLoading" size="mini" :header-cell-style="{background:'#F3F4F7',color:'#555'}" row-key='id'>
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ dateFormatVal(props.row.taskDateUpdated) }}</span>
              </el-form-item>
              <el-form-item label="Master">
                <span>{{ props.row.masterVersion }}</span>
              </el-form-item>
              <el-form-item label="Worker">
                <span>{{ props.row.workerVersion }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.taskItems }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.loadMessage }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.runMessage }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column property="seqNo" label="" align="center" width="50"> </el-table-column>
        <el-table-column property="cluster" label="" align="center" width="100"> </el-table-column>
        <el-table-column property="registerTime" label="" align="center" width="150" :formatter="dateFormat"> </el-table-column>
        <el-table-column property="masterAddress" label="Master" align="center" min-width="150"> </el-table-column>
        <el-table-column property="workerAddress" label="Worker" align="center" min-width="150"> </el-table-column>
        <!-- <el-table-column property="heartBeatTime" label="" align="center" width="150" :formatter="dateFormat"> </el-table-column> -->
        <el-table-column property="lastRunDataTime" label="" align="center" width="150" :formatter="dateFormat"> </el-table-column>
        <el-table-column property="nextRunStartTime" label="" align="center" width="150" :formatter="dateFormat"> </el-table-column>
         <el-table-column property="loadState" label="" align="center" width="70">
          <template slot-scope="scope">
            <el-tag v-if='scope.row.loadState==="INIT"' size="mini"></el-tag>
            <el-tag v-if='scope.row.loadState==="START"' type="success" size="mini"></el-tag>
            <el-tag v-if='scope.row.loadState==="WAIT_START"' size="mini"></el-tag>
            <el-tag v-if='scope.row.loadState==="STOP"' type="danger" size="mini"></el-tag>
            <el-tag v-if='scope.row.loadState==="ERROR"' type="danger" size="mini"></el-tag>
          </template>
        </el-table-column>
        <el-table-column property="runState" label="" align="center" width="70">
          <template slot-scope="scope">
            <el-tag v-if='scope.row.runState==="INIT"' size="mini"></el-tag>
            <el-tag v-if='scope.row.runState==="RUNNING"' type="success" size="mini"></el-tag>
            <el-tag v-if='scope.row.runState==="WAITING"' type="warning" size="mini"></el-tag>
            <el-tag v-if='scope.row.runState==="SLEEP"'  type="warning" size="mini"></el-tag>
            <el-tag v-if='scope.row.runState==="ERROR"' type="danger" size="mini"></el-tag>
            <el-tag v-if='scope.row.runState==="DEAD"' type="danger" size="mini"></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>

    <el-drawer :title="'['+currentObject.taskName+']'" :visible.sync="detailShow" direction="ttb" class="expandDrawer" size='300'>
      <el-form :inline="true" style='padding:0px 30px'>
        <el-row>
          <el-col :span="6">
            <el-form-item label="">
              {{currentObject.dealSysCode}}
            </el-form-item>
          </el-col>
         <el-col :span="6">
            <el-form-item label="Bean">
              {{currentObject.dealBizBeanName}}
            </el-form-item>
          </el-col>
           <el-col :span="6">
            <el-form-item label="">
              {{currentObject.processorType}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="">
              {{currentObject.permitRunStartTime}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="">
              {{currentObject.threadNumber}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="">
              {{currentObject.threadNumber}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="">
              {{currentObject.fetchDataNumber}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="">
              {{currentObject.executeNumber}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="6">
            <el-form-item label="(s)">
              {{currentObject.sleepTimeNoData}}
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="(s)">
              {{currentObject.judgeDeadInterval}}
            </el-form-item>
          </el-col>
           <el-col :span="6">
            <el-form-item label="">
              {{currentObject.taskItems}}
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="18">
            <el-form-item label="">
              {{currentObject.taskParameter}}
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-drawer>

  </div>
</template>

<script>
import moment from 'moment'
import CronInput from '../../components/cron/cron-input'

export default {
  data () {
    return {
      tableLoading: false,
      runtimeTableLoading: false,
      dialogBatchShow: false,
      batchOperationFalg: false,
      detailShow: false,
      dialogTitle: '',
      runtimeDialogTitle: '',
      dialogVisible: false,
      runtimeDialogVisible: false,
      EditType: 'new',
      queryObj: {
        state: '',
        cluster: '',
        pageNum: 0,
        pageSize: 10
      },
      detailObject: {
        cluster: '',
        dealSysCode: '',
        tag: '',
        processorType: '',
        forceCronExec: '',
        taskName: '',
        dealBizBeanName: '',
        permitRunStartTime: '',
        sleepTimeNoData: '',
        sleepTimeInterval: '',
        fetchDataNumber: '',
        executeNumber: '',
        assignNum: '',
        threadNumber: '',
        remark: '',
        taskItems: '',
        taskParameter: ''
      },
      currentObject: {},
      runtimeList: [],
      dataList: [],
      total: 0,
      totalData: [],
      allSysCodes: [],
      allTagCodes: [],
      taskChangeStateMap: {},
      taskChangeResultMap: {},
      clusterList: [],
      taskRuntimeTime: '',
      rule: [{ required: true, message: '', trigger: 'blur,change' }]
    }
  },
  mounted () {
    this.loadAllSysCodes()
    this.onQuery()
    this.loadAllCluster()
    this.loadAllTagCodes()
  },
  methods: {
    changeCron: function (val) {
      this.$set(this.detailObject, 'permitRunStartTime', val)
    },
    dateFormat: function (row, column) {
      var date = row[column.property]
      if (date === undefined || !date) {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },
    dateFormatVal: function (date) {
      if (date === undefined) {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },
    loadAllSysCodes () {
      this.$http.get('/api/system/loadAllSysCodes').then(res => {
        this.allSysCodes = res
      })
    },
    loadAllTagCodes () {
      this.$http.get('/api/tag/loadAllTagCodes').then(res => {
        this.allTagCodes = res
      })
    },
    onQuery () {
      this.tableLoading = true
      this.$http
        .post('/api/task/getAll', this.queryObj)
        .then(res => {
          this.tableLoading = false
          this.dataList = res.list
          var tmpChangeStateMap = {}
          var tmpChangeResultMap = {}
          for (var i = 0; i < this.dataList.length; i++) {
            tmpChangeStateMap[this.dataList[i].id] = false
            tmpChangeResultMap[this.dataList[i].id + 'succCount'] = 0
            tmpChangeResultMap[this.dataList[i].id + 'failCount'] = 0
          }
          this.taskChangeStateMap = tmpChangeStateMap
          this.taskChangeResultMap = tmpChangeResultMap
          this.total = res.total
        })
        .catch(errors => {
          this.dataList = []
          this.$message.error(errors.msg)
          this.tableLoading = false
        })
    },
    newObject () {
      this.detailObject = {}
      this.$set(this.detailObject, 'processorType', 'Execute')
      this.$set(this.detailObject, 'heartBeatRate', 5)
      this.$set(this.detailObject, 'sleepTimeNoData', 0)
      this.$set(this.detailObject, 'sleepTimeInterval', 0)
      this.$set(this.detailObject, 'permitRunStartTime', '0 0/1 * * * ?')
      if (this.allTagCodes && this.allTagCodes.length === 1) {
        this.$set(this.detailObject, 'tag', this.allTagCodes[0])
      }
      if (this.clusterList && this.clusterList.length === 1) {
        this.$set(this.detailObject, 'cluster', this.clusterList[0].cluster)
      }
      this.dialogTitle = ''
      this.dialogVisible = true
      this.EditType = 'new'
    },
    editObject (row) {
      this.dialogTitle = ''
      this.dialogVisible = true
      this.EditType = 'edit'
      this.detailObject = JSON.parse(JSON.stringify(row))
    },
    copyObject (row) {
      this.dialogTitle = '()'
      this.dialogVisible = true
      this.EditType = 'copy'
      this.detailObject = JSON.parse(JSON.stringify(row))
    },
    runtimeInfo (row) {
      let data = {}
      data.env = row.env
      data.cluster = row.cluster
      data.taskName = row.taskName
      this.runtimeTableLoading = true
      this.runtimeDialogVisible = true
      this.runtimeDialogTitle = '[' + row.taskName + '](3)'
      this.runtimeList = []
      let _this = this
      if (this.taskRuntimeTime) {
        clearInterval(this.taskRuntimeTime)
      }
      this.$http
        .post('/api/task/taskRuntime', data)
        .then(res => {
          this.runtimeList = res
          this.runtimeTableLoading = false

          this.taskRuntimeTime = setInterval(() => {
            _this.$http
              .post('/api/task/taskRuntime', data)
              .then(res2 => {
                _this.runtimeList = res2
              })
              .catch(errors => {
                _this.$message.error(errors.msg)
              })
          }, 1000)
        })
        .catch(errors => {
          this.$message.error(errors.msg)
          this.runtimeTableLoading = false
        })

      setTimeout(() => {
        _this.runtimeDialogVisible = false
      }, 1000 * 60 * 3)
    },
    runtimeDialogClose () {
      if (this.taskRuntimeTime) {
        clearInterval(this.taskRuntimeTime)
      }
    },
    validateObj () {
      this.$refs['objForm'].validate(valid => {
        if (valid) {
          this.save()
        } else {
          this.$message.error('')
          return false
        }
      })
    },
    save () {
      var url = ''
      if (this.EditType === 'new' || this.EditType === 'copy') {
        url = '/api/task/'
        this.detailObject['state'] = 'STOP'
        this.$http
          .post(url, this.detailObject)
          .then(res => {
            this.dialogVisible = false
            this.$message.success({
              duration: 3 * 1000,
              showClose:true,
              offset:80,
              message:''
            })
            this.onQuery()
          })
          .catch(errors => {
            this.$message.error(errors.msg)
          })
      } else {
        url = '/api/task/'
        this.$http
          .put(url, this.detailObject)
          .then(res => {
            this.dialogVisible = false
            this.$message.success({
              duration: 3 * 1000,
              showClose:true,
              offset:80,
              message:''
            })
            this.onQuery()
          })
          .catch(errors => {
            this.$message.error(errors.msg)
          })
      }
    },
    deleteObject (row) {
      if (row.state === 'START') {
        this.dialogVisible = false
        this.$message({
          message: '',
          duration: 3 * 1000,
          showClose:true,
          offset:80,
          type: 'error'
        })
        return
      }
      this.$http
        .delete('/api/task/' + row.cluster + '/' + row.dealSysCode + '/' + row.taskName)
        .then(res => {
          this.dialogVisible = false
          this.$message({
            message: '',
            duration: 3 * 1000,
            showClose:true,
            offset:80,
            type: 'success'
          })
          this.onQuery()
        })
        .catch(errors => {
          this.$message.error(errors.msg)
        })
    },
    onShow (row) {
      this.currentObject = row
      this.detailShow = true
    },
    loadAllCluster () {
      this.$http.get('/api/cluster/getAllCluster').then(res => {
        this.clusterList = res
      })
    },
    stopTask (row) {
      this.$http
        .put('/api/task/stop/' + row.cluster + '/' + row.dealSysCode + '/' + row.taskName, {})
        .then(res => {
          this.dialogVisible = false
          this.$message.success({
            duration: 3 * 1000,
            showClose:true,
            offset:80,
            message:''
          })
          this.refreshRuntimeState(row, 'STOP')
        })
        .catch(errors => {
          this.$message.error(errors.msg)
        })
    },
    startTask (row) {
      this.$http
        .put('/api/task/start/' + row.cluster + '/' + row.dealSysCode + '/' + row.taskName, {})
        .then(res => {
          this.dialogVisible = false
          this.$message.success({
            duration: 3 * 1000,
            showClose:true,
            offset:80,
            message:''
          })
          this.refreshRuntimeState(row, 'START')
        })
        .catch(errors => {
          this.$message.error(errors.msg)
        })
    },
    refreshRuntimeState (row, state) {
      let data = {}
      data.cluster = row.cluster
      data.taskName = row.taskName
      let assignNum = row.assignNum
      let id = row.id
      let _this = this
      _this.taskChangeStateMap[id] = true
      _this.taskChangeResultMap[id + 'succCount'] = 0
      _this.taskChangeResultMap[id + 'failCount'] = 0
      var time = setInterval(() => {
        _this.$http
          .post('/api/task/taskRuntime', data)
          .then(res => {
            if (
              (state === 'STOP' && res.length === 0) ||
              (state === 'START' && assignNum === res.length)
            ) {
              let failCount = 0
              let succCount = 0
              for (var i = 0; i < res.length; i++) {
                if (res[i].state === 'ERROR') {
                  failCount = failCount + 1
                } else {
                  succCount = succCount + 1
                }
              }
              _this.taskChangeResultMap[id + 'succCount'] = succCount
              _this.taskChangeResultMap[id + 'failCount'] = failCount
              _this.taskChangeStateMap[id] = false
              window.clearInterval(time)
              _this.onQuery()
            }
            _this.runtimeList = res
          })
          .catch(errors => {
            _this.taskChangeStateMap[id] = false
            window.clearInterval(time)
          })
      }, 3000)
      // $oncebeforeDestroy
      this.$once('hook:beforeDestroy', () => {
        clearInterval(time)
      })
    }

  },
  components: {
    CronInput
  }
}
</script>

