instance.interceptors.response.use(
    response => {
        if (response.data && response.data.flag === "S") {
            // 
            if (response.data.code === '407') {
                MessageBox.confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    // store.dispatch('LogOut').then(() => {
                    //     location.reload() // vue-router bug
                    // })
                    //store.dispatch('LogOut')
                    // const {
                    //     protocol,
                    //     host,
                    // } =  window.parent.location

                    // window.parent.location.href = `${protocol}//${host}/login`

                }).catch(() => {
                    return Promise.reject({msg: response.data.msg, code: response.data.code, data: response.data.data})
                });
            }

            let res = response.data.data || []
            if (res instanceof Object) {
                res.__successMsg = response.data.msg // 
                res.__code = response.data.code // 
                res.__flag = response.data.flag // 
            } else {
                // 
            }
            return Promise.resolve(res)
        }
        return Promise.reject({msg: response.data.msg, code: response.data.code, data: response.data.data})
    },
    error => {
        return Promise.reject(error)
    }
);
export default instance
